<template>
  <div>
    <mt-field label="姓名" placeholder="请输入姓名" v-model="ruleForm.Name"></mt-field>
    <mt-field label="电话" placeholder="请输入手机号" type="tel" v-model="ruleForm.Phone"></mt-field>
    <mt-field label="公司座机" placeholder="请输入公司座机" v-model="ruleForm.company_phone"></mt-field>
    <mt-field label="公司名称" placeholder="请输入公司名称" v-model="ruleForm.company_name"></mt-field>
    <mt-field label="公司地址" placeholder="请输入公司地址" v-model="ruleForm.company_address"></mt-field>
    <mt-field label="住宅地址" placeholder="请输入住宅地址" v-model="ruleForm.house_address"></mt-field>
    <mt-field label="预约时间" placeholder="请输入预约时间" type="date" v-model="ruleForm.time"></mt-field>
    <div style="margin-top: 50px">
      <mt-button @click="submitForm" size="large" type="primary">提交</mt-button>
    </div>
    <div style="margin-top: 50px">
      <mt-button @click="goBackDetails" size="large" type="default">返回主页</mt-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      ruleForm: {
        Name: '', Phone: '', company_phone: '', company_name: '', company_address: '', house_address: '', time: '',
      }
    }
  },
  methods: {
    submitForm() {
      for(const v in this.ruleForm) {
        if (this.ruleForm[v] === ''){ this.$message.warning(`${v}选项不能为空！`); return }
      }
      this.ruleForm.time = new Date(this.ruleForm.time).getTime();
      $.ajax({
        dataType: 'json',
        data: JSON.stringify(this.ruleForm),
        type: 'post',
        url: 'http://95.169.26.44:8088/api/v1/add/user/info',
        contentType: 'application/json',
      }).done((data)=>{
        if (data.msg) { this.$message.success('提交成功！'); this.goBackDetails() }
      }).fail((res)=>{ this.$message.error('服务器报错！') });
    },
    goBackDetails(){
      this.$router.push({ path: '/creditCarDetail' });
    },
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.toTop{
  margin-top: 10px;
}
</style>



